.form{
    
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    color: whitesmoke;
  

    @media all and(min-width:920px){

    }
    
    

    &__label{
       height: 50px;
       width: 300px;
       font-size: 20px;
       padding: 25px;

       @media all and (min-width: 724px){
        font-size: 30px;
        height: 70px;
        width: 600px;
        
       }
       
       
    }
    &__input{
        align-self: flex-start;
        height: 40px;
       width: 300px;
       align-content: center;
       border-radius: 10px;
       margin-bottom: 20px;

        @media all and (min-width: 724px){
        align-self: center;
        font-size: 30px;
        height: 50px;
        width: 400px;
        margin-left: 100px;
       }
       
       

    }
}